import React, {Component} from "react";
import {connect} from "react-redux";
import {
    fetchComparatorConfig,
    fetchComparatorDevicesList,
    fetchProducers
} from "eshop/modules/simfree/actions/comparator";
import {getComparatorConfig, getComparatorDevices, getProducers} from "eshop/modules/simfree/selectors";
import ComparatorSectionComponent from "./ComparatorSectionComponent";
import ComparatorDeviceTab from "./ComparatorDeviceTab";
import ComparatorEmptyDeviceTab from "./ComparatorEmptyDeviceTab";
import ComparatorAddDeviceModal from "eshop/modules/simfree/components/comparator/ComparatorAddDeviceModal";
import ActionType from "eshop/modules/simfree/fetchComparatorConfigActionType";
import {getMarketContextPrefixUrl} from "eshop/modules/configurator/selectors/metaData"
import {setMarketContext} from "eshop/modules/configurator/actions/filters";

class OplComparatorMainComponent extends Component {

    constructor(props) {
        super(props);
        this.state = ({markDiff: false});
        this.props.setMarketContext(this.props.marketContext);
    }

    componentDidMount() {
        this.props.getDeviceProducers();
        this.props.getComparatorConfig("", this.props.component);


    }

    handleMarkDifferences() {
        this.state.markDiff ? this.setState({markDiff: false}) : this.setState({markDiff: true});
    }

    renderDevicesHeader() {
        let emptyDeviceTab = [];
        for (let i = 0; i < 3 - this.props.comparatorDevices.length; i++) {
            emptyDeviceTab.push(<ComparatorEmptyDeviceTab producers={this.props.producers} index={i}
                                                          key={"deviceTab" + i} comparatorUid={this.props.component}
                                                          compareDevices={this.props.devicesComparison}/>)
        }
        console.log(emptyDeviceTab);
        return (
            <div className="l-full-row">
                <div className="l-page-row">
                    <a className="u-inline-block u-text-decoration-none u-padding-top-m"
                       href={this.props.marketContextPrefixUrl + this.props.goBackToShopUrl}>{this.props.goBackToShop}</a>
                    <h2 className="h1 u-padding-top-l-xl">{this.props.devicesComparison}</h2>
                    <div className="l-row">
                        <div className="l-col l-col-12  opl-compare-products__section u-margin-l">
                            {this.props.comparatorDevices && this.props.comparatorDevices.map((device, index) =>
                                <ComparatorDeviceTab device={device} index={index} key={"deviceTab" + index}
                                                     comparatorUid={this.props.component}/>
                            )}
                            {emptyDeviceTab}
                        </div>
                    </div>
                    {this.props.comparatorDevices.length > 0 ?
                        <label className="opl-checkbox o-checkbox u-margin-l-xl">
                            <input type="checkbox" onChange={this.handleMarkDifferences.bind(this)}/>
                            <span className="o-ci"></span>
                            <span className="o-ci-label">{this.props.markDifferences}</span>
                        </label>
                        :
                        null
                    }
                </div>
            </div>
        )
    }

    renderComparatorConfig() {
        return (
            <div id="comparator-expander-parent" className="l-full-row u-margin-xxl">
                <div className="l-page-row">
                    <div className="o-separator g-gray4-bgc"/>
                    {this.props.comparatorConfig.comparatorSectionDTOList &&
                    <div id="comparator-expander" data-js-module="common/modules/opl-expander" role="tablist"
                         data-js-options="{&quot;triggerSelector&quot;:&quot;.opl-section__expander-trigger&quot;,&quot;contentSelector&quot;:&quot;.opl-section__content&quot;,&quot;parentClass&quot;:&quot;opl-section--expander&quot;}">
                        {this.props.comparatorConfig.comparatorSectionDTOList.map((section, index) =>
                            <ComparatorSectionComponent section={section} idx={index}
                                                        markDiff={this.state.markDiff}/>
                        )}
                    </div>
                    }
                </div>
            </div>
        )
    }

    render() {
        return (
            <div>
                {this.renderDevicesHeader()}
                {this.renderComparatorConfig()}
                <ComparatorAddDeviceModal
                    comparatorDevices={this.props.producers}/>
            </div>
        )
    }

}

const mapStateToProps = state => ({
    comparatorConfig: getComparatorConfig(state),
    comparatorDevices: getComparatorDevices(state),
    producers: getProducers(state),
    marketContextPrefixUrl: getMarketContextPrefixUrl(state),
});


const mapDispatchToProps = (dispatch) => ({
    setMarketContext: (marketContext) => dispatch(setMarketContext(marketContext)),
    getComparatorConfig: (device, pk) => dispatch(fetchComparatorConfig(device, pk, ActionType.VISIT_PAGE)),
    getDeviceProducers: () => dispatch(fetchProducers()),
    fetchComparatorDevices: () => dispatch(fetchComparatorDevicesList())
});

export default connect(mapStateToProps, mapDispatchToProps)(OplComparatorMainComponent);